import React from 'react';

import Typography from '@mui/material/Typography';
import Section from '../Section';

const training = [
    "Laravel 9 Online Course",
    "Python 3 Online Course",
    "React with Redux Online Course",
    "iOS 12 & Swift 5 Online Course",
    "The Association for Overseas Technical Cooperation and Sustainable Partnerships (AOTS)",
    "Philippine Charity Sweepstakes Office (On-the-Job Training)"
];

function Training() {

    return (
        <Section title="TRAINING & ONLINE COURSE">
            {training.map((v,i) => <Typography variant="body1" key={i}>• {v}</Typography>)}
        </Section>
    );
}

export default Training;