import React from "react";

import { styled } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import AppBar from '@mui/material/AppBar';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Avatar from '@mui/material/Avatar';

import Button from '@mui/material/Button';
import DownloadIcon from '@mui/icons-material/Download';

import WindowDimension from './WindowDimension';

import Section from './Section';
import Contact from './parts/Contact';
import Experience from "./parts/Experience";
import Education from "./parts/Education";
import Skill from './parts/Skill';
import Language from './parts/Language';
import Reference from './parts/Reference';
import Training from './parts/Training';
import Description from './parts/Description';

const MainContainer = styled(Container)(() => ({
    padding: '0 !important'
}));

const MainBox = styled(Box)(({ height }) => ({
    height: height, 
    padding: 0
}));

const Header = styled(AppBar)(() => ({
    backgroundColor: '#1F4E79',
    marginTop: '15px',
    padding: '20px 0px',
    verticalAlign: 'middle'
}));


function Home() {

    const { height, width } = WindowDimension();

    return (
        <React.Fragment>
            <CssBaseline />
            <MainContainer maxWidth="lg">
                <MainBox height={height}>
                    <Stack spacing={4}>
                        <Header position="static">
                            <Grid container spacing={3}>
                                <Grid item xs={12} md={4} />
                                <Grid item xs={12} md={8} sx={{textAlign: 'center'}}>
                                    <Typography variant="h4" sx={{ color: '#FFE599'}}>
                                        EARL GLENN C. PAPARON
                                    </Typography>
                                    <Typography variant="h5">
                                        FULL STACK DEVELOPER
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Header>
                        <Grid container sx={{ marginTop: '0px !important', padding: '0px' }}>
                            {width >= 900 ? (
                                <>
                                    <Grid item md={4}>
                                        <div style={{ padding: '30px', height: '200px' }}>
                                            <Avatar
                                                alt="Profile Picture"
                                                src="/profile.JPG"
                                                sx={{ 
                                                    width: 250, 
                                                    height: 250, 
                                                    top: -100,
                                                    margin: '0 auto'
                                                }}
                                            />
                                        </div>
                                        <Contact style={{ marginTop: '200px' }} />
                                        <Skill />
                                        <Language />
                                        <Reference />
                                    </Grid>
                                    <Grid item md={8}>
                                        <Description width={width} />
                                        <Experience />
                                        <Education width={width} />
                                        <Training />
                                    </Grid>
                                </>
                            ) : (
                                <>
                                    <Grid item xs={12}>
                                        <div style={{ padding: '10px', height: 'unset' }}>
                                            <Avatar
                                                alt="Profile Picture"
                                                src="/profile.JPG"
                                                sx={{ 
                                                    width: 150, 
                                                    height: 150, 
                                                    top: 0,
                                                    margin: '0 auto'
                                                }}
                                            />
                                        </div>
                                        <Description width={width} />
                                        <Contact style={{ marginTop: 'unset' }} />
                                        <Experience />
                                        <Education width={width} />
                                        <Training />
                                        <Skill />
                                        <Language />
                                        <Reference />
                                    </Grid>
                                </>
                            )}
                        </Grid>
                    </Stack>
                </MainBox>
            </MainContainer>
        </React.Fragment>
    );
}

export default Home;