import React from 'react';

import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';

import Section from '../Section';

function Experience() {

    return (
        <Section title="EXPERIENCE">
            <Grid container spacing={0}>
                <Grid item xs={7} md={8}>
                    <Typography variant="body1">
                        <strong>Inforesta Philippines Inc.</strong>
                    </Typography>
                    <Typography variant="body1" sx={{ fontStyle: 'italic', color: 'gray' }}>
                        Makati City, Philippines
                    </Typography>
                </Grid>
                <Grid item xs={5} md={4} sx={{ textAlign: 'right' }}>
                    <Typography variant="body1">
                        2010 - Present
                    </Typography>
                </Grid>
                <Grid item xs={12}><br /></Grid>
                <Grid item md={12}>
                    <Typography variant="body1">
                        <strong>Web Developer</strong>
                    </Typography>
                    <Typography variant="subtitle2">
                        • AWS virtual server management
                    </Typography>
                    <Typography variant="subtitle2">
                        • Database administration
                    </Typography>
                    <Typography variant="subtitle2">
                        • Developing frontend and backend web applications
                    </Typography>
                </Grid>
                <Grid item xs={12}><br /><Divider />< br /></Grid>
                <Grid item xs={7} md={8}>
                    <Typography variant="body1">
                        <strong>Inforesta Co., Ltd.</strong>
                    </Typography>
                    <Typography variant="body1" sx={{ fontStyle: 'italic', color: 'gray' }}>
                        Tokyo, Japan
                    </Typography>
                </Grid>
                <Grid item xs={5} md={4} sx={{ textAlign: 'right' }}>
                    <Typography variant="body1">
                        2015 - 2021
                    </Typography>
                </Grid>
                <Grid item xs={12}><br /></Grid>
                <Grid item md={12}>
                    <Typography variant="body1">
                        <strong>Leading System Engineer</strong>
                    </Typography>
                    <Typography variant="subtitle2">
                        • AWS virtual server management
                    </Typography>
                    <Typography variant="subtitle2">
                        • Database administration
                    </Typography>
                    <Typography variant="subtitle2">
                        • Developing frontend and backend web applications
                    </Typography>
                    <Typography variant="subtitle2">
                        • FTP Server, VPN Server, Mail Server and Web Server
                    </Typography>
                </Grid>
            </Grid>
        </Section>
    );
}

export default Experience;