import React from 'react';

import Chip from '@mui/material/Chip';

import Section from '../Section';

const skill = [
    "PHP",
    "Python 3",
    "JavaScript",
    "CSS",
    "HTML",
    "ReactJS",
    "Redux",
    "Material UI",
    "Laravel",
    "WordPress",
    "Ruby on Rails",
    "AWS",
    "SQL Server",
    "Apache / IIS",
    "Windows",
    "Ubuntu",
    "CentOS",
    "MacOS"
];

function Skill() {

    return (
        <Section title="SKILL">
            {skill.map((v, i) => <Chip label={v} color="primary" variant="outlined" sx={{ margin: '5px' }} key={i} />)}
        </Section>
    );
}

export default Skill;