import React from 'react';

import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import DownloadIcon from '@mui/icons-material/Download';
import Grid from '@mui/material/Grid';

import Section from '../Section';

function Description(props) {

    const { width } = props;

    return (
        <Section>
            <Grid container spacing={0}>
                <Grid item xs={12} sx={{ textAlign: width >= 900 ? 'right' : 'center', marginBottom: '10px' }}>
                    <Button variant="contained" startIcon={<DownloadIcon />} href="/resume.pdf" target="_blank">
                        Download Resume
                    </Button>
                </Grid>
                <Grid item xs={12}>
                    <Typography variant="body2">
                        As a Full Stack Developer, I've surely mastered a number of coding languages, including Python, PHP, JavaScript, CSS, HTML, and other web development frameworks like ReactJS, Redux, and Laravel. I've likely created a variety of online apps, such as e-commerce websites, in addition to others.
                    </Typography>
                </Grid>
            </Grid>
        </Section>
    );
}

export default Description;