import React from 'react';

import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';

import Section from '../Section';

function Education(props) {

    const { width } = props;

    return (
        <Section title="EDUCATION">
            <Grid container spacing={0}>
                <Grid item xs={12} md={8}>
                    <Typography variant="body1">
                        <strong>AMA University Online Education</strong>
                    </Typography>
                    <Typography variant="body1">
                        Bachelor of Science in Computer Science
                    </Typography>
                </Grid>
                <Grid item xs={12} md={4} sx={{ textAlign: width >= 900 ? 'right' : 'unset' }}>
                    <Typography variant="body1">
                        December 2022 - Present
                    </Typography>
                </Grid>
                <Grid item xs={12}><br /><Divider />< br /></Grid>
                <Grid item xs={12} md={8}>
                    <Typography variant="body1">
                        <strong>Informatics College</strong>
                    </Typography>
                    <Typography variant="body1">
                        Bachelor of Science in Information Technology
                    </Typography>
                </Grid>
                <Grid item xs={12} md={4} sx={{ textAlign: width >= 900 ? 'right' : 'unset' }}>
                    <Typography variant="body1">
                        June 2014 – August 2014
                    </Typography>
                    <Typography variant="body1" sx={{ fontStyle: 'italic', color: 'gray' }}>
                        Undergraduate
                    </Typography>
                </Grid>
            </Grid>
        </Section>
    );
}

export default Education;